<template>
  <div class="edit-page">
    <el-row>
      <el-col>{{value.id != '0' ? '编辑' : '新建'}}{{config.title}}</el-col>
    </el-row>
    <el-row>
      <el-col>
        <ncform :form-schema="config.formSchema" v-model="formValue" form-name="editform"></ncform>
      </el-col>
    </el-row>
    <el-row class="btn-area" type="flex">
      <el-col>
      </el-col>
      <el-button type="success" @click="submitData()" plain>{{value.id != '0' ? '保存' : '新建'}}</el-button>
      <el-button type="info" @click="goBack()" plain>返回</el-button>
    </el-row>
  </div>
</template>

<script>
import _get from "lodash-es/get";
import { ncformUtils } from "ncform-common";

export default {
  components: {},
  created() {
    this.$data.value[this.config.idField] = this.$route.params[
      this.config.idField
    ];
    this.initData();
  },
  props: {
    config: {
      type: Object,
      default: {}
    }
  },
  data() {
    return {
      value: {},
      onlyId: "",
      formValue: {}
    };
  },
  computed: {
    isEdit() {
      return this.$data.onlyId !== "0";
    }
  },
  methods: {
    goBack() {
      this.$router.go(-1);
    },
    initData() {
      this.$data.formValue = {};

      this.$data.onlyId = this.value[this.config.idField];
      if (this.$data.onlyId != "0") {
        this.loadFormData();
      }
    },
    loadFormData() {
      const formDataConfig = this.config.formData;
      const data = {};
      formDataConfig.params.forEach(item => {
        data[item.name] = ncformUtils.smartAnalyze(item.value, {
          data: [
            {
              symbol: "$id",
              value: this.$data.onlyId
            }
          ]
        });
      });

      this.$axios.post(formDataConfig.apiUrl, data).then(res => {
        const resField = formDataConfig.resField;
        this.$data.formValue = resField ? _get(res.data, resField) : res.data;
      });
    },
    submitData() {
      const submitConfig = this.config.buttons.submit;
      let data = {};
      if (submitConfig.valueField) {
        data[submitConfig.valueField] = this.$data.formValue;
      } else {
        data = Object.assign({}, this.$data.formValue);
      }

      data[submitConfig.idField || "id"] = this.$data.onlyId;
      this.$axios.post(submitConfig.apiUrl, data).then(res => {
        this.$message({
          type: "success",
          message: "保存成功"
        });
        this.goBack();
      });
    }
  },
  watch: {
    $route: {
      handler() {
        const value = {};
        value[this.config.idField] = this.$route.params[this.config.idField];
        this.$data.value = value;
      }
    },
    value: {
      handler() {
        this.initData();
      }
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" rel="stylesheet/scss" scoped>
.btn-area {
  margin-top: 8px;
}
</style>



