<template>
  <div class="list">
    <list-page :config="config" :storeValueName="storeValueName"></list-page>
  </div>
</template>

<script>
import listPage from "common/components/list-page.vue";

export default {
  components: {
    listPage
  },
  data() {
    return {
      storeValueName: "demo.value",

      config: {
        // 查询条件
        query: {
          normal: {
            // 普通搜索
            // ncform的配置
            type: "object",
            properties: {
              name: {
                type: "string ",
                ui: {
                  label: "关键字",
                  placeholder: "普通关键字"
                }
              }
            }
          }
        },

        // 工具栏，位于列表之上
        toolbar: {
          batchActions: {
            // 批量操作，当selectAll为true时有效
            delete: {
              name: "批量删除",
              enable: true, // 支持dx表达式
              handler: {
                type: "ajax", // ajax / page / modal / component
                options: {
                  // type = 'ajax'
                  apiUrl: "",
                  params: [
                    {
                      name: "id",
                      value: "dx: {{$item.id}}"
                    }
                  ]
                },
                refresh: true // 操作后是否刷新数据
              }
            },
            others: [
              // 参考 Action Object config
            ]
          },
          tools: {
            new: {
              name: "新建",
              enable: true, // 支持dx表达式
              handler: {
                type: "page", // ajax / page / modal / component
                options: {
                  // type = 'page'
                  route: "dx: '/demo/edit/0'"
                },
                refresh: true // 操作后是否刷新数据
              }
            },
            others: [
              // 参考 Action Object config
            ]
          }
        },

        // 显示列表
        list: {
          selectAll: true, // 是否全选
          datasource: {
            // 数据源
            apiUrl: "/api/getListData", // 请求url
            paramFields: {
              // 传过去的参数
              pageSize: "pageSize", // 每页显示的项数
              pageNum: "pageNum", // 请求的页码
              query: "" // 查询条件，当为空时，查询对象的每个Key就是字段。当提供值时，即整个查询对象作为值传过去
            },
            otherParams: {
              // 其它的请求参数，如 type: 1
            },
            resField: {
              // 返回结果字段
              pageingTotal: "page.total", // 分页总数字段
              list: "data" // 列表数据字段
            }
          },
          columns: [
            //
            {
              header: "日期", // 表头
              dataField: "date" // 数据源的字段
            },
            {
              header: "姓名", // 表头
              dataField: "name" // 数据源的字段
            },
            {
              header: "地址", // 表头
              dataField: "address" // 数据源的字段
            }
          ],
          actions: {
            // 项操作
            delete: {
              // 查看
              enable: true, // 或 dx表达式，如 dx: !!{{$item.id}}
              handler: {
                type: "modal",
                options: {
                  component: {
                    // 对于弹窗，需要指定弹窗页面的业务组件，这里约定弹窗页面整个就是一个业务组件
                    name: "common.modals.edit-modal",
                    config: {
                      title: "商品", // 标题，editMode不同，显示也不同。比如这里写“商品”，编辑模式下显示为编辑商品，新建模式下显示新建商品
                      idField: "id", // 该表单记录的唯一标识，通过该字段可判断编辑模式还是新建模式
                      formData: {
                        // 当编辑模式时取表单数据用
                        apiUrl: "/api/getListData", // 取表单数据的Url
                        resField: "",
                        params: [
                          {
                            name: "userid",
                            value: "123"
                          }
                        ]
                      },
                      formSchema: {
                        type: "object",
                        properties: {
                          name: {
                            type: "string "
                          }
                        }
                      },
                      buttons: {
                        submit: {
                          // 提交
                          apiUrl: "/api/getListData", // 提交的Url
                          idField: "",
                          valueField: "" // 当为空时，即表单的每个一级字段即为参数名
                        },
                        back: {
                          // 返回
                          enable: true
                        }
                      }
                    },
                    value: {
                      id: "dx: {{$item.id}}"
                    }
                  },
                  modalConfig: {
                    title: "弹窗title",
                    buttons: {
                      confirm: {
                        enable: true
                      },
                      cancel: {
                        enable: true
                      },
                      others: [
                        {
                          enable: true,
                          name: "自定义取消",
                          eventName: "commonEditCancel",
                          close: true
                        }
                      ]
                    }
                  }
                }
                // 参考 Action Object config 的 handler( type = 'modal' 或 'page')
              }
            },
            view: {
              // 查看
              enable: true, // 或 dx表达式，如 dx: !!{{$item.id}}
              handler: {
                type: "component",
                options: {
                  component: {
                    // 对于弹窗，需要指定弹窗页面的业务组件，这里约定弹窗页面整个就是一个业务组件
                    name: "demo.components.say-hi",
                    config: {
                      title: "商品", // 标题，editMode不同，显示也不同。比如这里写“商品”，编辑模式下显示为编辑商品，新建模式下显示新建商品
                      idField: "id", // 该表单记录的唯一标识，通过该字段可判断编辑模式还是新建模式
                      formData: {
                        // 当编辑模式时取表单数据用
                        apiUrl: "/api/getListData", // 取表单数据的Url
                        resField: "",
                        params: [
                          {
                            name: "userid",
                            value: "123"
                          }
                        ]
                      },
                      formSchema: {
                        type: "object",
                        properties: {
                          name: {
                            type: "string "
                          }
                        }
                      },
                      buttons: {
                        submit: {
                          // 提交
                          apiUrl: "/api/getListData", // 提交的Url
                          idField: "",
                          valueField: "" // 当为空时，即表单的每个一级字段即为参数名
                        },
                        back: {
                          // 返回
                          enable: true
                        }
                      }
                    },
                    value: {
                      id: "dx: {{$item.id}}"
                    }
                  },
                  modalConfig: {
                    title: "弹窗title",
                    buttons: {
                      confirm: {
                        enable: true,
                        name: "确定",
                        eventName: "_commonEditConfirm",
                        close: true
                      },
                      cancel: {
                        enable: true,
                        name: "取消",
                        eventName: "_commonEditCancel",
                        close: true
                      },
                      others: [
                        {
                          enable: true,
                          name: "取消02",
                          eventName: "_commonEditCancel",
                          close: true
                        }
                      ]
                    }
                  }
                }
                // 参考 Action Object config 的 handler( type = 'modal' 或 'page')
              }
            },
            edit: {
              // 编辑
              enable: true,
              handler: {
                type: "page",
                options: {
                  route: "dx: '/demo/edit/'+{{$item.id}}"
                }
                // 参考 Action Object config 的 handler( type = 'modal' 或 'page')
              }
            },
            others: [
              // 参考 Action Object config
            ]
          }
        }
      }
    };
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" rel="stylesheet/scss" scoped>

</style>



