// 404 页面
import notFound from 'common/components/notfound.vue';

// 模块页面
import demoList from 'modules/demo/pages/list.vue';
import demoEdit from 'modules/demo/pages/edit.vue'

// 根目录
const rootPath = '';

// 页面路由
const routes = [
  // 默认首页
  { path: '', redirect: { name: 'demo-list' } },

  // 模块相关页面
  { path: '/demo/list', component: demoList, name: 'demo-list' },
  { path: '/demo/edit/:id', component: demoEdit, name: 'demo-edit' },
].map(route => {
  route.path = rootPath + route.path;
  return route;
});

// 404 页
routes.push({ path: '*', component: notFound, name: 'notfound' });

export default routes;



