<template>
  <div class="list-page">
    <list :config="config" v-model="valueData"></list>
  </div>
</template>

<script>
import { mapState } from "vuex";
import _get from 'lodash-es/get';
import list from "configurable/list-page.vue";

export default {
  created() {
    this.$data.valueData = Object.assign(this.$data.valueData, _get(this.$store.state, this.storeValueName));
  },
  props: {
    config: {
      type: Object,
      default: {}
    },
    storeValueName: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      valueData: {
        pageNum: 1,
        pageSize: 20,
        query: {}
      }
    };
  },
  components: {
    list
  },
  watch: {
    valueData: {
      handler(newValue) {
        this.$store.commit(this.storeValueName.replace(/\./g,'/'), newValue);
      },
      deep: true
    }
  }
};
</script>

<style lang="scss" rel="stylesheet/scss" scoped>

</style>



