<template>
    <div class="listTpl">
        <edit-page :config="config"></edit-page>
    </div>
</template>

<script>
import editPage from "configurable/edit-page.vue";

export default {
  data() {
    return {
      config: {
        title: "商品", // 标题，editMode不同，显示也不同。比如这里写“商品”，编辑模式下显示为编辑商品，新建模式下显示新建商品
        idField: "id", // 该表单记录的唯一标识，通过该字段可判断编辑模式还是新建模式
        formData: {
          // 当编辑模式时取表单数据用
          apiUrl: "/api/getInfo", // 取表单数据的Url
          resField: "data",
          params: [
            {
              name: "userid",
              value: "123"
            }
          ]
        },
        formSchema: {
          type: "object",
          properties: {
            name: {
              type: "string "
            }
          }
        },
        buttons: {
          submit: {
            // 提交
            apiUrl: "/api/getListData", // 提交的Url
            idField: "userxx",
            valueField: "" // 当为空时，即表单的每个一级字段即为参数名
          },
          back: {
            // 返回
            enable: true
          }
        }
      }
    };
  },

  components: {
    editPage
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" rel="stylesheet/scss" scoped>

</style>



