<template>
  <div class="list">
    <el-row type="flex" align="bottom" class='mbSpace' v-if="seachBarVisible">
      <el-col>
        <!-- 普通搜索配置 -->
        <ncform :form-schema="config.query.normal" v-model="normalQueryValue" form-name="normalQuery"></ncform>
        <!-- 高级搜索配置 -->
        <ncform v-if="advSearchBarVisible" v-show="showAdvSearch" :form-schema="config.query.adv" v-model="advQueryValue" form-name="advQuery"></ncform>
      </el-col>   
      <div class="searchBtnArea">
        <!-- 搜索按钮区域 -->
        <el-button type="info" class="searchBtn mgb10" icon="el-icon-refresh" @click="resetList()" plain>重置</el-button>
        <el-button type="info" v-if="advSearchBarVisible" v-show="!showAdvSearch" class="searchBtn mgb10" icon="el-icon-plus" @click="showAdvSearch = !showAdvSearch" plain>展开</el-button>
        <el-button type="info" v-if="advSearchBarVisible" v-show="showAdvSearch" class="searchBtn mgb10" icon="el-icon-minus" @click="showAdvSearch = !showAdvSearch" plain>收起</el-button>
        <el-button type="primary" class="searchBtn" icon="el-icon-search" plain @click="search">搜索</el-button>
      </div>
    </el-row>

    <el-row>
      <el-col :span="24">
        <el-row type="flex">
          <template v-if="batchActionsVisible">
            <!-- 批量操作 按钮 -->
            <action-object v-if="config.toolbar.batchActions.delete" :config="config.toolbar.batchActions.delete" :selected="multipleSelection">
              <el-button v-if="config.toolbar.batchActions.delete.enable" :disabled="this.$data.multipleSelection.length == 0" type="danger" icon="el-icon-delete" @click="eventHandler(config.toolbar.batchActions.delete.handler, null, multipleSelection)" plain>
                {{config.toolbar.batchActions.delete.name}}
              </el-button>
            </action-object>
            <template v-if="config.toolbar.batchActions.others">
              <action-object v-for="(item, key) in config.toolbar.batchActions.others" :config="item" :key="key" :selected="multipleSelection">
                <el-button :disabled="this.$data.multipleSelection.length == 0" v-if="item.enable" type="info" @click="eventHandler(item.handler, null, multipleSelection)" plain>
                  {{item.name}}
                </el-button>
              </action-object>
            </template>
          </template>

          <!-- 下面这个el-col用于flex布局，勿删 -->
          <el-col>
          </el-col>
          <!-- 新建等 常规按钮 -->
          <template v-if="toolsVisible">
            <action-object v-if="config.toolbar.tools.new" :config="config.toolbar.tools.new">
              <el-button v-if="config.toolbar.tools.new.enable" type="success" icon="el-icon-document" @click="eventHandler(config.toolbar.tools.new.handler)" plain>
                {{config.toolbar.tools.new.name}}
              </el-button>
            </action-object>
            <template v-if="config.toolbar.tools.others">
              <action-object v-for="(item, key) in config.toolbar.tools.others" :key="key" :config="item">
                <el-button v-if="item.enable" type="info" @click="eventHandler(item.handler)" plain>
                  {{item.name}}
                </el-button>
              </action-object>
            </template>
          </template>
        </el-row>
      </el-col>
    </el-row>

    <el-row>
      <el-col :span="24">
        <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%" @selection-change="handleSelectionChange">
          <!-- 全选 -->
          <el-table-column v-if="config.list.selectAll" type="selection">
          </el-table-column>
          <!-- 其他表头 -->
          <el-table-column v-for="(item, key) in config.list.columns" :key="key" :label="item.header">
            <template slot-scope="scope">
              <template v-if="!item.component">
                {{ scope.row[item.dataField] }}
              </template>
              <template v-else>
                <component v-bind:is="item.component.name.replace(/\./g, '_')" :config="item.component.config" :value="item.component.value"></component>
              </template>
            </template>
          </el-table-column>
          <!-- 操作 -->
          <el-table-column align="center" label="操作">
            <template slot-scope="scope">
              <action-object v-if="config.list.actions.view" :config="config.list.actions.view" :item="scope.row">
                <el-button v-if="config.list.actions.view.enable" type="text" size="small" @click="eventHandler(config.list.actions.view.handler, scope.row)">查看</el-button>
              </action-object>
              <action-object v-if="config.list.actions.edit" :config="config.list.actions.edit" :item="scope.row">
                <el-button v-if="config.list.actions.edit.enable" type="text" size="small" @click="eventHandler(config.list.actions.edit.handler, scope.row)">编辑</el-button>
              </action-object>
              <action-object v-if="config.list.actions.delete" :config="config.list.actions.delete" :item="scope.row">
                <el-button v-if="config.list.actions.delete.enable" type="text" size="small" @click="eventHandler(config.list.actions.delete.handler, scope.row)">删除</el-button>
              </action-object>
            </template>
          </el-table-column>
        </el-table>
      </el-col>
    </el-row>

    <el-row v-if="pagingVisible">
      <!-- 页码 -->
      <el-col align="right" style="margin-top:30px;">
        <el-pagination background @size-change="handleSizeChange" @current-change="currentChange" layout="sizes, prev, pager, next" :page-count="pageCount" :current-page="value.pageNum " :page-sizes="[20, 40, 60, 80, 100]">
        </el-pagination>
      </el-col>
    </el-row>

    <modal
      :visible.sync="modal.visible"
      :modal-config="modal.modalConfig"
    >
      <component 
        slot-scope="modalProps"
        class="modalComponent" 
        :is="modal.name.replace(/\./g, '_')" 
        :config="modal.config" 
        :value="modal.value" 
        :modalId="modalProps.modalId"
      >
      </component>
    </modal>
  </div>
</template>

<script>
import _get from "lodash-es/get";
import { ncformUtils } from "ncform-common";
import actionObject from "./action-object.vue";
import modal from './modal.vue';

export default {
  components: {
    actionObject,
    modal
  },
  props: {
    config: {
      type: Object,
      default: () => ({})
    },
    value: {
      type: Object,
      default: () => ({
        pageNum: 1,
        pageSize: 20,
        query: {}
      })
    }
  },
  created() {
    // 初始化搜索内容
    this.$data.normalQueryValue = JSON.parse(JSON.stringify(this.value.query));
    this.$data.advQueryValue = JSON.parse(JSON.stringify(this.value.query));
  },
  mounted() {
    // 加载数据
    this.loadtableData();
  },

  data() {
    return {
      pageCount: 1, // 页码总数
      tableData: [], // 表单数据
      multipleSelection: [], // 多选的选中项
      showAdvSearch: false, //  搜索模式，0为普通搜索, 1为高级搜索
      normalQueryValue: {}, // 常规搜索value
      advQueryValue: {}, // 高级搜索value
      modal: {
        visible: false,
        componentName: "",
        config: {},
        value: {},
        modalConfig: {}
      }
    };
  },
  computed: {
    // 搜索栏 是否显示
    seachBarVisible() {
      return this.config.query && this.config.query.normal;
    },
    // 高级搜索 是否显示
    advSearchBarVisible() {
      return (
        this.config.query && this.config.query.normal && this.config.query.adv
      );
    },
    batchActionsVisible() {
      return (
        this.config.list.selectAll &&
        this.config.toolbar &&
        this.config.toolbar.batchActions
      );
    },
    toolsVisible() {
      return this.config.toolbar && this.config.toolbar.tools;
    },
    pagingVisible() {
      return !this.config.paging || this.config.paging.enable;
    }
  },
  methods: {
    // 加载表格数据
    loadtableData() {
      const dataSource = this.config.list.datasource;

      let postData = {};
      postData[dataSource.paramFields.pageSize] = this.value.pageSize;
      postData[dataSource.paramFields.pageNum] = this.value.pageNum;
      if (dataSource.paramFields.query) {
        postData[dataSource.paramFields.query] = this.value.query;
      } else {
        postData = Object.assign(postData, this.value.query);
      }
      this.$axios.post(dataSource.apiUrl, postData).then(res => {
        const listField = dataSource.resField.list;
        const pageingTotalField = dataSource.resField.pageingTotal;
        this.$data.tableData = listField
          ? _get(res.data, `${listField}`)
          : res.data;
        this.$data.pageCount = pageingTotalField
          ? _get(res.data, `${pageingTotalField}`)
          : 1;
      });
    },
    // 重置页面
    resetList(){
      this.normalQueryValue = {};
      this.advQueryValue = {};
      this.value.pageNum=1;
      this.value.query = {};
      this.loadtableData();
    },
    // 多选改变时触发
    handleSelectionChange(val) {
      this.$data.multipleSelection = val;
    },
    // Action Object config 实现
    eventHandler(handler, item = {}, multipleSelection = []) {
      const { type, options } = handler;
      const handlerData = [
        {
          symbol: "$item",
          value: item
        },
        {
          symbol: "$selected",
          value: multipleSelection
        }
      ];
      switch (type) {
        case "ajax":
          const params = {};
          options.params.forEach(item => {
            params[item.name] = ncformUtils.smartAnalyze(item.value, {
              data: handlerData
            });
          });
          this.$axios.post(handler.options.apiUrl, params).then(res => {
            if (handler.refresh) {
              this.loadtableData();
            }
          });
          break;
        case "page":
          const path = ncformUtils.smartAnalyze(options.route, {
            data: handlerData
          });
          this.$router.push({
            path
          });
          break;
        case "modal":
          const newValue = {};
          for (let key in options.component.value) {
            newValue[key] = ncformUtils.smartAnalyze(
              options.component.value[key],
              {
                data: handlerData
              }
            );
          }

          this.$data.modal = Object.assign(
            {},
            this.$data.modal,
            options.component
          );

          this.$data.modal.value = newValue;

          this.$data.modal.modalConfig = options.modalConfig;
          this.$data.modal.visible = true;
          break;
      }
    },
    // 页码变化
    currentChange(currentIndex) {
      this.value.pageNum = currentIndex;
      this.loadtableData();
    },
    // pageSize变化
    handleSizeChange(size) {
      this.value.pageNum = 1;
      this.value.pageSize = size;
      this.loadtableData();
    },
    // 搜索
    search() {
      this.value.pageNum = 1;
      this.value.query = Object.assign(
        {},
        this.value.query,
        this.$data.normalQueryValue,
        this.$data.advQueryValue
      );
      this.loadtableData();
    },
  },
  watch: {
    value: {
      handler: function(newVal) {
        this.$emit("input", newVal);
      },
      deep: true
    }
  }
};
</script>

<style lang="scss" rel="stylesheet/scss" scoped>
.mbSpace {
  margin-bottom: 20px;
}
.searchBtnArea {
  width: 90px;
  margin-left: 20px;
}
.el-button.searchBtn {
  height: 40px;
  margin: 0;
  margin-left: 0;
}
.el-button.mgb10 {
  margin-bottom: 10px;
  margin-left: 0;
}
</style>



